'use client';

import Link from 'next/link';
import { useEffect, useMemo, useRef, useState } from 'react';
import { useParams } from 'next/navigation';
import {
  DndContext,
  DragEndEvent,
  DragOverEvent,
  DragStartEvent,
  PointerSensor,
  TouchSensor,
  pointerWithin,
  rectIntersection,
  useDroppable,
  useSensor,
  useSensors,
} from '@dnd-kit/core';
import {
  SortableContext,
  arrayMove,
  horizontalListSortingStrategy,
  useSortable,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import type { ColumnId } from '@/lib/types';
import { getColumns } from '@/lib/storage';
import { NavShell } from '@/app/components/NavShell';
import { useWorkspace } from '@/lib/firebase/useWorkspace';
import { subscribeCards } from '@/lib/firebase/cardsLive';
import { updateCard } from '@/lib/firebase/cards';

type UiCard = {
  id: string;
  boardId: string;
  title: string;
  description?: string;
  priority: string;
  source: string;
  columnId: ColumnId;
  order: number;
  artifacts?: unknown[];
  startedDoingAt?: number;
  movedToReviewAt?: number;
  timeToReviewMs?: number;
};

type ColumnSpec = { id: ColumnId; name: string };

type CardData = {
  type: 'card';
  columnId: ColumnId;
};

function SortableCard({ card, tone }: { card: UiCard; tone?: 'blocked' }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({
    id: card.id,
    data: { type: 'card', columnId: card.columnId } satisfies CardData,
  });

  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.55 : 1,
  };

  const toneClass =
    tone === 'blocked'
      ? 'border border-red-500/35 bg-red-500/10'
      : '';

  return (
    <div ref={setNodeRef} style={style} className={`card p-3 ${toneClass}`}>
      <div className="flex items-start gap-2">
        <button
          aria-label="Drag"
          title="Drag"
          data-testid={`card-drag-${card.id}`}
          className="mt-0.5 w-8 h-8 grid place-items-center rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.35)] text-[color:var(--muted)]"
          {...attributes}
          {...listeners}
        >
          <span className="text-lg leading-none">≡</span>
        </button>

        <Link
          href={`/board/${encodeURIComponent(card.boardId)}/task/${encodeURIComponent(card.id)}`}
          className="block flex-1"
        >
          <div className="font-extrabold leading-snug">{card.title}</div>
          <div className="mt-2 flex flex-wrap gap-2">
            <span className={`chip ${card.priority === 'P0' || card.priority === 'P1' ? 'chip-red' : ''}`}>
              {card.priority}
            </span>
            <span className="chip">{card.source}</span>
          </div>
          <div className="mt-2 text-xs text-[color:var(--muted)] line-clamp-2">{card.description ?? '—'}</div>
        </Link>
      </div>
    </div>
  );
}

function Column({ col, cards }: { col: ColumnSpec; cards: UiCard[] }) {
  const { setNodeRef, isOver } = useDroppable({
    id: col.id,
    data: { type: 'column', columnId: col.id },
  });

  return (
    <div
      ref={setNodeRef}
      className={`panel p-3 min-w-[260px] w-[260px] ${isOver ? 'outline outline-2 outline-[rgba(91,140,255,.45)]' : ''}`}
      data-column={col.id}
      data-testid={`column-${col.id}`}
    >
      <div className="flex items-center justify-between border-b border-[color:var(--stroke)] pb-2">
        <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">{col.name}</div>
        <div className="chip">{cards.length}</div>
      </div>
      <div className="mt-3 grid gap-2 min-h-[40px]">
        {cards.map((c) => (
          <SortableCard key={c.id} card={c} />
        ))}
      </div>
    </div>
  );
}

function BlockedLane({ col, cards }: { col: ColumnSpec; cards: UiCard[] }) {
  const { setNodeRef, isOver } = useDroppable({
    id: col.id,
    data: { type: 'column', columnId: col.id },
  });

  return (
    <div
      ref={setNodeRef}
      className={`panel p-3 ${isOver ? 'outline outline-2 outline-red-500/40' : ''}`}
      data-column={col.id}
      data-testid={`column-${col.id}`}
    >
      <div className="flex items-center justify-between border-b border-red-500/20 pb-2">
        <div className="text-xs uppercase tracking-wide text-red-300/90 font-extrabold">{col.name} (attention)</div>
        <div className="chip chip-red">{cards.length}</div>
      </div>

      <div className="mt-3 flex gap-3 overflow-x-auto pb-2 min-h-[80px]">
        {cards.length ? (
          cards.map((c) => (
            <div key={c.id} className="min-w-[260px] w-[260px]">
              <SortableCard card={c} tone="blocked" />
            </div>
          ))
        ) : (
          <div className="text-sm text-[color:var(--muted)]">No blocked tasks.</div>
        )}
      </div>
    </div>
  );
}

function normalizeColumnOrders(all: UiCard[], columnId: ColumnId) {
  const inCol = all
    .filter((c) => c.columnId === columnId)
    .sort((a, b) => a.order - b.order)
    .map((c, idx) => ({ ...c, order: idx }));
  const others = all.filter((c) => c.columnId !== columnId);
  return [...others, ...inCol];
}

function resolveOverColumnId(columns: ColumnSpec[], all: UiCard[], overId: string): ColumnId | null {
  const overCard = all.find((c) => c.id === overId);
  if (overCard) return overCard.columnId;
  const col = columns.find((c) => c.id === (overId as ColumnId));
  return col ? col.id : null;
}

function canMoveAcrossColumns(fromCol: ColumnId, toCol: ColumnId) {
  // Review -> Done is gated: only task-level Approve action can complete.
  if (fromCol === 'review' && toCol === 'done') return false;
  return true;
}

function moveCardAcrossColumns(all: UiCard[], activeId: string, toCol: ColumnId, overId: string) {
  const current = [...all];
  const moving = current.find((c) => c.id === activeId);
  if (!moving) return all;

  const fromCol = moving.columnId;
  if (fromCol === toCol) return all;
  if (!canMoveAcrossColumns(fromCol, toCol)) return all;

  const nextFrom = current
    .filter((c) => c.columnId === fromCol && c.id !== activeId)
    .sort((a, b) => a.order - b.order);

  const nextTo = current
    .filter((c) => c.columnId === toCol && c.id !== activeId)
    .sort((a, b) => a.order - b.order);

  const overCard = current.find((c) => c.id === overId);
  const insertIndex = overCard && overCard.columnId === toCol ? nextTo.findIndex((c) => c.id === overId) : nextTo.length;

  const movedCard: UiCard = { ...moving, columnId: toCol };
  nextTo.splice(Math.max(0, insertIndex), 0, movedCard);

  const normalizedFrom = nextFrom.map((c, idx) => ({ ...c, order: idx }));
  const normalizedTo = nextTo.map((c, idx) => ({ ...c, order: idx }));

  const others = current.filter((c) => c.columnId !== fromCol && c.columnId !== toCol && c.id !== activeId);
  return [...others, ...normalizedFrom, ...normalizedTo];
}

export default function BoardPage() {
  const params = useParams<{ boardId: string }>();
  const boardId = decodeURIComponent(params.boardId);
  const { workspaceId } = useWorkspace();

  const [cards, setCards] = useState<UiCard[]>([]);
  const [draggingId, setDraggingId] = useState<string | null>(null);

  // Avoid overwriting optimistic drag state while a drag is in flight.
  const draggingRef = useRef(false);

  const dragMetaRef = useRef<{ activeId: string | null; fromCol: ColumnId | null }>({
    activeId: null,
    fromCol: null,
  });

  useEffect(() => {
    if (!workspaceId) return;
    return subscribeCards(workspaceId, boardId, (c) => {
      if (draggingRef.current) return;
      setCards(c as unknown as UiCard[]);
    });
  }, [workspaceId, boardId]);

  const columns = useMemo(() => getColumns() as ColumnSpec[], []);

  const byCol = useMemo(() => {
    const map: Record<ColumnId, UiCard[]> = { proposed: [], queued: [], doing: [], review: [], blocked: [], done: [] };
    for (const c of cards) map[c.columnId].push(c);
    for (const k of Object.keys(map) as ColumnId[]) map[k] = map[k].sort((a, b) => a.order - b.order);
    return map;
  }, [cards]);

  const sensors = useSensors(
    useSensor(PointerSensor, { activationConstraint: { distance: 5 } }),
    useSensor(TouchSensor, { activationConstraint: { delay: 180, tolerance: 8 } })
  );

  function onDragStart(ev: DragStartEvent) {
    const activeId = String(ev.active.id);
    draggingRef.current = true;
    dragMetaRef.current.activeId = activeId;
    dragMetaRef.current.fromCol = cards.find((c) => c.id === activeId)?.columnId ?? null;
    setDraggingId(activeId);
  }

  function onDragOver(ev: DragOverEvent) {
    const { active, over } = ev;
    if (!over) return;

    const activeId = String(active.id);
    const overId = String(over.id);

    setCards((prev) => {
      const toCol = resolveOverColumnId(columns, prev, overId);
      if (!toCol) return prev;

      const moving = prev.find((c) => c.id === activeId);
      if (!moving) return prev;
      if (moving.columnId === toCol) return prev;
      if (!canMoveAcrossColumns(moving.columnId, toCol)) return prev;

      return moveCardAcrossColumns(prev, activeId, toCol, overId);
    });
  }

  async function onDragEnd(ev: DragEndEvent) {
    const { active, over } = ev;
    setDraggingId(null);

    if (!over || !workspaceId) {
      draggingRef.current = false;
      return;
    }

    const activeId = String(active.id);
    const overId = String(over.id);

    const activeCard = cards.find((c) => c.id === activeId);
    if (!activeCard) {
      draggingRef.current = false;
      dragMetaRef.current = { activeId: null, fromCol: null };
      return;
    }

    const originalFromCol = dragMetaRef.current.fromCol ?? activeCard.columnId;
    const toCol = resolveOverColumnId(columns, cards, overId) ?? activeCard.columnId;

    if (!canMoveAcrossColumns(originalFromCol, toCol)) {
      draggingRef.current = false;
      dragMetaRef.current = { activeId: null, fromCol: null };
      return;
    }

    // Case A: reorder within same column (dropping on the column itself moves to bottom)
    if (originalFromCol === toCol) {
      const colCards = cards.filter((c) => c.columnId === toCol).sort((a, b) => a.order - b.order);
      const oldIndex = colCards.findIndex((c) => c.id === activeId);
      let newIndex = colCards.findIndex((c) => c.id === overId);
      if (newIndex === -1) newIndex = colCards.length - 1;

      if (oldIndex === -1 || newIndex === -1 || oldIndex === newIndex) {
        draggingRef.current = false;
        dragMetaRef.current = { activeId: null, fromCol: null };
        return;
      }

      const moved = arrayMove(colCards, oldIndex, newIndex).map((c, idx) => ({ ...c, order: idx }));

      setCards((prev) =>
        prev.map((c) => {
          const m = moved.find((x) => x.id === c.id);
          return m ? { ...c, order: m.order } : c;
        })
      );

      await Promise.all(moved.map((m) => updateCard(workspaceId, boardId, m.id, { order: m.order })));
      draggingRef.current = false;
      dragMetaRef.current = { activeId: null, fromCol: null };
      return;
    }

    // Case B: moved across columns — persist the card's columnId + normalized orders
    let normalized = cards;

    // If onDragOver didn't run (or we dropped very fast), ensure the card is in the target column.
    if (activeCard.columnId !== toCol) {
      normalized = moveCardAcrossColumns(normalized, activeId, toCol, overId);
    }

    normalized = normalizeColumnOrders(normalized, originalFromCol);
    normalized = normalizeColumnOrders(normalized, toCol);

    setCards(normalized);

    const toPersist = normalized.filter((c) => c.columnId === originalFromCol || c.columnId === toCol);

    await Promise.all(
      toPersist.map((c) => {
        const patch: Record<string, unknown> = {
          columnId: c.columnId,
          order: c.order,
        };

        if (c.id === activeId) {
          const now = Date.now();
          if (originalFromCol !== 'doing' && toCol === 'doing' && typeof c.startedDoingAt !== 'number') {
            patch.startedDoingAt = now;
          }
          if (toCol === 'review') {
            patch.movedToReviewAt = now;
            const startedDoingAt = typeof c.startedDoingAt === 'number' ? c.startedDoingAt : undefined;
            if (typeof startedDoingAt === 'number' && now >= startedDoingAt) {
              patch.timeToReviewMs = now - startedDoingAt;
            }
          }
        }

        return updateCard(workspaceId, boardId, c.id, patch);
      })
    );

    draggingRef.current = false;
    dragMetaRef.current = { activeId: null, fromCol: null };
  }

  return (
    <NavShell title="Board">
      <div className="flex items-center justify-between gap-3">
        <div className="text-xs text-[color:var(--muted)]">Desktop + mobile • Drag to reorder or move across columns</div>
        <div className="flex items-center gap-2">
          <Link
            href={`/board/${encodeURIComponent(boardId)}/settings`}
            className="px-4 py-2 rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] font-extrabold"
          >
            Settings
          </Link>
          <Link href="/intake" className="btn-primary px-4 py-2 rounded-xl font-extrabold">
            + Intake
          </Link>
        </div>
      </div>

      <div className="mt-4">
        <DndContext
          sensors={sensors}
          collisionDetection={(args) => {
            const activeId = args.active?.id;
            const pointer = pointerWithin(args).filter((c) => c.id !== activeId);

            // Prefer dropping over column containers when the pointer is within them.
            const pointerColumns = pointer.filter((c) => {
              const dc = args.droppableContainers.find((d) => d.id === c.id);
              return dc?.data?.current?.type === 'column';
            });

            if (pointerColumns.length) return pointerColumns;
            if (pointer.length) return pointer;
            return rectIntersection(args);
          }}
          onDragStart={onDragStart}
          onDragOver={onDragOver}
          onDragEnd={onDragEnd}
        >
          <div className="grid gap-4">
            {(() => {
              const blockedCol = columns.find((c) => c.id === 'blocked');
              if (!blockedCol) return null;
              const blockedCards = byCol.blocked;
              return (
                <SortableContext
                  key={blockedCol.id}
                  id={blockedCol.id}
                  items={blockedCards.map((c) => c.id)}
                  strategy={horizontalListSortingStrategy}
                >
                  <BlockedLane col={blockedCol} cards={blockedCards} />
                </SortableContext>
              );
            })()}

            <div className="overflow-x-auto">
              <div className="flex gap-4 min-w-max pb-3">
                {columns
                  .filter((c) => c.id !== 'blocked')
                  .map((col) => {
                    const colCards = byCol[col.id];
                    return (
                      <SortableContext
                        key={col.id}
                        id={col.id}
                        items={colCards.map((c) => c.id)}
                        strategy={verticalListSortingStrategy}
                      >
                        <Column col={col} cards={colCards} />
                      </SortableContext>
                    );
                  })}
              </div>
            </div>
          </div>
        </DndContext>
      </div>

      {draggingId ? <div className="sr-only">Dragging {draggingId}</div> : null}
    </NavShell>
  );
}
