import type { Board, Card, ColumnId } from './types';

const KEY = 'tars-kanban:v2'; // bump to clear seeded sample data

type Store = {
  boards: Board[];
  activeBoardId: string;
  cards: Card[];
};

export function defaultStore(): Store {
  return {
    boards: [],
    activeBoardId: '',
    cards: [],
  };
}

export function loadStore(): Store {
  if (typeof window === 'undefined') return defaultStore();
  try {
    const raw = window.localStorage.getItem(KEY);
    if (!raw) return defaultStore();
    const parsed = JSON.parse(raw);
    if (!parsed || !parsed.boards || !parsed.cards) return defaultStore();
    return parsed as Store;
  } catch {
    return defaultStore();
  }
}

export function saveStore(store: Store) {
  if (typeof window === 'undefined') return;
  window.localStorage.setItem(KEY, JSON.stringify(store));
}

export function normalizeOrders(cards: Card[], columnId: ColumnId): Card[] {
  const inCol = cards
    .filter((c) => c.columnId === columnId)
    .sort((a, b) => a.order - b.order)
    .map((c, idx) => ({ ...c, order: idx }));
  const others = cards.filter((c) => c.columnId !== columnId);
  return [...others, ...inCol];
}

export function getColumns(): { id: ColumnId; name: string }[] {
  return [
    { id: 'proposed', name: 'Proposed' },
    { id: 'queued', name: 'Queued' },
    { id: 'doing', name: 'Doing' },
    { id: 'review', name: 'Review' },
    { id: 'blocked', name: 'Blocked' },
    { id: 'done', name: 'Done' },
  ];
}
